<script>
import api from "@/common/api.js";
var setBeatTime, menuTime, mobileSystem, vCode;
export default {
	created() {
		// #ifdef APP-PLUS
		plus.navigator.closeSplashscreen(); 
		// #endif 
	},
	onLaunch: function() {
		mobileSystem = 'web';
		//console.log('App Launch');
		/* 版本检测s */
		// #ifdef APP-PLUS
		uni.getSystemInfo({
		    success: (e)=> {
				mobileSystem = e.platform;
				plus.runtime.getProperty(plus.runtime.appid, (i)=>{
					vCode = i.versionCode;
					// 读取API版本号
					uni.request({
						url: api.apiData.getVersion,
						method: 'GET',
						success: (o) => {
							var res = o.data.Data;
							if(mobileSystem == 'ios'){
								// 苹果
								var myCode = res.apple.versionCode;
							}else{
								// 安卓
								var myCode = res.android.versionCode;
							}
							if(myCode > vCode){
								// 圆点 
								uni.showTabBarRedDot({ index: 3 });
								var isNewVersion = true;
							}else{
								var isNewVersion = false;
							}
							uni.setStorage({
							    key: 'isNewVersion',
							    data: isNewVersion
							});
						}
					});
				});
		    }
		});
		// #endif 
	},
	onShow: function() {
		console.log('App Show')
		// 中间按钮点击事件
		uni.onTabBarMidButtonTap(()=> {
			this.goToRec();
		});
		// 是否登录
		var info = api.getLogins();
		var userId = (info==false) ? 0 : info.userId;
		// 创建数据表
		// #ifdef APP-PLUS
		/*api.openDb('video_'+ api.appkey);
		api.createTable();
		menuTime = setInterval(() => {
			this.getDownVideoList(userId);
		}, 5000);*/
		// schemes
		var param = plus.runtime.arguments;
		if(param){
			//console.log('123')
			api.jumpUrl('/pages/recharge/recharge', 'new');
		} 
		// #endif
		/* 心跳start */
		if(info!=false){
			if(mobileSystem=='android'){
				var sys = 2;
			}else if(mobileSystem=='ios'){
				var sys = 3;
			}else{
				var sys = 1;
			}
			// 进入后先执行一次
			setTimeout(() => {
				this.getServer(sys, userId);
			}, 1);
			setBeatTime = setInterval(() => {
				this.getServer(sys, userId);
			}, 1*60*1000); // 1m一跳
		}else{
			//console.log('未登录状态下不发送心跳数据');
		}
		/* 心跳end */
	},
	onHide: function() {
		console.log('App Hide');
		// #ifdef APP-PLUS
		clearInterval(menuTime);
		// #endif
		clearInterval(setBeatTime);
	},
	methods: {
		goToRec() {
			var info = api.getLogins();
			if(info==false){
				api.showToast('请先登录', 1500);
				setTimeout(()=>{
					api.jumpUrl('/pages/login/login', 'new');
				}, 500);
			}else{
				api.jumpUrl('/pages/recharge/recharge', 'new');
			}
		},
		getServer(sys, userId) {
			uni.request({
				url: api.apiData.heartBeat+'/sys/'+sys+'/userId/'+userId,
				success: (e)=> {
					if(e.data.Data==0){
						var closeAppTime = 11;
						setInterval(()=> {
							if(closeAppTime<=0){
								//console.log('closeApp');
								api.outApp();
							}
							closeAppTime --;
							uni.showToast({
							    title: 'App即将关闭'+closeAppTime+'s',
							    duration: 50000,
								mask: true,
								image: '/static/stop.png'
							});
						}, 1000);
					} 
				},
				fail: ()=> {
					clearInterval(setBeatTime);
				}
			});
		},
		getDownVideoList(userId) {
			// #ifdef APP-PLUS
			plus.sqlite.selectSql({
				name: 'video',
				sql: 'select * from downVideoList where downstatus=0 and userid='+userId,
				success: (e) => {
					// 带数字
					uni.setTabBarBadge({
						index: 2,
						text: e.length+''
					});
				},
				fail: (e) => {
					//console.log('查询错误: '+JSON.stringify(e));
					clearInterval(menuTime);
				} 
			});
			// #endif
		}
	}
}
</script>
<style>
/* #ifndef APP-PLUS-NVUE */
	page{
		background-color: #FFFFFF;
	}
	uni-swiper .uni-swiper-dot{
		width: 5px!important;
		height: 5px!important;
	}
	uni-swiper .uni-swiper-dot-active{
		width: 12px!important;
		border-radius: 8upx!important;
	}
/* #endif */
</style>
